import React from 'react';
import { useState } from 'react';
import { Fragment } from 'react';

let firstMoveMade = false;
let isTie = false;

function Board({xIsNext, squares, onPlay}) {
  function handleClick(i){
    firstMoveMade = true;
    if(squares[i] || calculateWinner(squares)) return;
    const nextSquares = squares.slice();
    if(xIsNext){
      nextSquares[i] = 'X';
    } else {
      nextSquares[i] = 'O';
    }
    onPlay(nextSquares);
  }
  const winner = calculateWinner(squares);
  let status;
  if(isTie){
     status = 'X and O lose';
  } else if(winner){
    status = 'Winner: ' + winner;
  } else {
    status = (!firstMoveMade? 'First player: ': 'Next player: ') + (xIsNext? 'X': 'O');
  }
  function Square({value, onSquareClick}) {
    return ( 
    <button className="square" onClick={onSquareClick}>
      {value}
      </button> 
    );
  }
  function squareMaker(){
    const board = [];
    for(let i = 0; i <= 8; i++){
          board.push(
    <Square value = {squares[i]} onSquareClick={() => handleClick(i)} />)
        }
    return board;
  }
  return (
    <Fragment>
      <div className="status">{status}</div>
    {squareMaker()}
  </Fragment>
  );
}
export default function Game() {
  const [history, setHistory] = useState([Array(9).fill(null)]);
  const [currentMove, setCurrentMove] = useState(0);
  const xIsNext = currentMove % 2 === 0;
  const currentSquares = history[currentMove];
  function handlePlay(nextSquares) {
    const nextHistory = [...history.slice(0, currentMove + 1), nextSquares];
    setHistory(nextHistory);
    setCurrentMove(nextHistory.length - 1);
  }
  function jumpTo(nextMove){
    setCurrentMove(nextMove);
  }
  const moves = history.map((element, move) => {
    if(move === 9 && !calculateWinner(currentSquares)){
       isTie = true;
    }
    let description;
    if(move === history.length - 1){
      description = calculateWinner(currentSquares)? 
      calculateWinner(currentSquares) + ' is the winner!': move === 0? 'Play Tic Tac Toe': isTie? 'X and O lose': 'You are at move #' + move;
    } else if (move > 0) {
      description = 'Go to move #' + move;
    } else {
      description = 'Go to game start';
    }
    return (
      <li key={move}>
        { move === history.length - 1?<p>{description}</p>:
          <button className="jump-to-move-btn" onClick={() => jumpTo(move)}>{description}</button>
        }
      </li>
    )
  })
  return (
    <div className="game">
      <div className="game-board">
        <Board xIsNext={xIsNext} squares={currentSquares} onPlay={handlePlay} />
      </div>
        <ol className="game-info">{moves}</ol>
    </div>
  )
}
function calculateWinner(squares){
const lines = [
  [0,1,2],
  [3, 4, 5],
  [6, 7, 8],
  [0, 3, 6],
  [1, 4, 7],
  [2, 5, 8],
  [0, 4, 8],
  [2, 4, 6]
];
for(let i = 0; i < lines.length; i++){
  const [a, b, c] = lines[i];
  if(squares[a] && squares[a] === squares[b] && squares[a] === squares[c]){
    return squares[a];
}
}
return null;
};


